  // transform: rotate(45deg)
  // visibility: hidden
 }  // opacity: 0

.pagination {
  position: fixed;
  z-index: 15;
  top: 0;
  right: 0;
  height: 100%;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  &__item {
    margin: 10px; }
  &__link {
    font-size: 20px;
    &.is-active {
      color: green; } } }

